import { useFormContext } from './FormContext';
import getFormName from '../../../utils/getFormName';
import StatusModal from '../../../UI/StatusModal';
import  {Snackbar}  from '@mui/material';

const Confirmation = ({ onPrev }) => {
  const { 
    formData,
    sections,
    handleSubmit,
    levels,
    filteredMatriels,
    filteredUnits,
    errorAdding,
    snackbarOpen,
    setSnackbarOpen,
    snackbarMessage,
    isErrorOpen,
    setIsErrorOpen } = useFormContext();
  const levelName = getFormName(formData.level,levels);
  const materialName = getFormName(formData.material,filteredMatriels);
  const unitName = getFormName(formData.unit,filteredUnits);
  return (
    <div>
      <h2 className="text-2xl font-bold mb-4">Confirmation</h2>
      <p className="mb-4">Please review your information before submitting:</p>
      <ul className="list-disc list-inside mb-4">
        <li>level: {levelName}</li>
        <li>Trimester: {formData.Trimester}</li>
        <li>material: {materialName}</li>
        <li>unit: {unitName}</li>
        <li>difficulty: {formData.difficulty}</li>
      </ul>
      <div className="p-4">
      <h2 className="text-xl font-bold mb-4">The Exercise</h2>
      
      {sections.map((section, sectionIndex) => (
        <div key={sectionIndex} className="p-4 mb-4 bg-gray-100 rounded-lg shadow">
          <h3 className="text-lg font-semibold">Section {sectionIndex + 1}</h3>
          
          <p><strong>Content:</strong> {section.content || 'No content provided'}</p>
          
          <p><strong>Image Position:</strong> {section.imagePosition}</p>
          
          <p><strong>Images:</strong> 
          <div className="w-2/3 mx-auto flex">
          {section.image && 
          <img 
            className={`${section.image && section.secondImage ? 'w-1/2 m-3 my-auto' : 'w-2/3 mx-auto' }`}           
            src={section.image} 
            alt="Section" /> 
          }
          {section.secondImage && 
          <img 
            className={`${section.image && section.secondImage ? 'w-1/2 m-3 my-auto' : 'w-2/3 mx-auto' }`}
            src={section.secondImage} 
            alt="Section" /> 
          }
          </div>
          
          </p>
          
          <div className="mt-4">
            <h4 className="font-medium">Questions:</h4>
            {section.questions.length > 0 ? (
              section.questions.map((q, questionIndex) => (
                <div key={questionIndex} className="ml-4 my-2">
                  <p><strong>Question {questionIndex + 1}:</strong> {q.question}</p>
                  <p><strong>Answer:</strong> {q.answer}</p>
                </div>
              ))
            ) : (
              <p>No questions available</p>
            )}
          </div>
        </div>
      ))}
    </div>
      <div className="flex justify-between">
        <button
          onClick={onPrev}
          className="bg-gray-500 hover:bg-gray-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
        >
          Previous
        </button>
        <button
          onClick={handleSubmit}
          className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
        >
          Submit
        </button>
      </div>
      { isErrorOpen && (
            <StatusModal
              type="error"
              title="Error Occurredr"
              message={errorAdding}
              isOpen={isErrorOpen}
              onClose={() => setIsErrorOpen(false)}
            />
             )}
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={() => setSnackbarOpen(false)}
        message={snackbarMessage}
      />
    </div>
  );
};

export default Confirmation;