export const calculateImageDimensions = (imageSrc, targetWidth) => {
  const img = new Image();
  img.src = imageSrc;
  const aspectRatio = img.height / img.width;
  const width = targetWidth;
  const height = width * aspectRatio;
  return { width, height };
};

export const addContentToPdf = (pdf, content, x, y, width) => {
  const contentLines = pdf.splitTextToSize(content, width);
  pdf.text(contentLines, x, y);
  return contentLines.length * 6;
};

export const addQuestionsToPdf = (pdf, questions, x, y, width) => {
  let yPosition = y;
  questions.forEach((q, index) => {
    const questionText = `${index + 1}. ${q.question}`;
    const questionLines = pdf.splitTextToSize(questionText, width);
    pdf.text(questionLines, x, yPosition);
    yPosition += questionLines.length * 6;
  });
  return yPosition - y;
};