import React from "react";
import jsPDF from "jspdf";
import { calculateImageDimensions, addContentToPdf, addQuestionsToPdf } from "../utils/pdfUtils";
import {Download } from 'lucide-react';

const ExamPdf = ({ exercises }) => {
  const generatePdf = () => {
    const pdf = new jsPDF();
    const padding = 10;
    const pageWidth = pdf.internal.pageSize.width - padding * 2;
    const pageHeight = pdf.internal.pageSize.height - 10;
    let yPosition = 10;
    let exerciseNumber = 1;
  
    exercises.forEach((exercise) => {
      if (yPosition > pageHeight) {
        pdf.addPage();
        yPosition = 10;
      }
      //exercise number 
      pdf.setFontSize(14);
      pdf.setFont("Helvetica", "bold");
      pdf.text(`Exercise ${exerciseNumber}`, padding, yPosition);
      yPosition += 10;

      exercise.exercise.forEach((contentItem) => {
        
        if (yPosition > pageHeight) {
          pdf.addPage();
          yPosition = 10;
        }
  
        pdf.setFontSize(12);
        pdf.setFont("Helvetica", "normal");
  
        if (["left", "right"].includes(contentItem.imagePosition)) {
          yPosition = handleSideImageLayout(pdf, contentItem, padding, pageWidth, yPosition);
        } else {
          yPosition = handleTopBottomImageLayout(pdf, contentItem, padding, pageWidth, yPosition);
        }
  
        yPosition += 10;
      });
      exerciseNumber++;
      // Check space after each exercise
      if (yPosition > pageHeight) {
        pdf.addPage();
        yPosition = 10;
      }
    });
  
    pdf.save(`exam.pdf`);
  };
  
  const handleSideImageLayout = (pdf, contentItem, padding, pageWidth, yPosition) => {
    const targetWidth = 80;
    const { height: imageHeight } = calculateImageDimensions(contentItem.image, targetWidth);
    const textWidth = pageWidth - targetWidth - 10;
  
    if (contentItem.imagePosition === "left") {
      // Draw image on the left
      pdf.addImage(contentItem.image, "JPEG", padding, yPosition, targetWidth, imageHeight);
      const textX = padding + targetWidth + 10;
      yPosition += addContentToPdf(pdf, contentItem.content, textX, yPosition, textWidth);
      yPosition += addQuestionsToPdf(pdf, contentItem.questions, textX, yPosition, textWidth);
    } else {
      // Draw text first, then image on the right
      yPosition += addContentToPdf(pdf, contentItem.content, padding, yPosition, textWidth);
      yPosition += addQuestionsToPdf(pdf, contentItem.questions, padding, yPosition, textWidth);
      pdf.addImage(
        contentItem.image,
        "JPEG",
        padding + textWidth + 10,
        yPosition - imageHeight,
        targetWidth,
        imageHeight
      );
    }
  
    return yPosition + imageHeight + 10; // Return updated yPosition
  };
  
  const handleTopBottomImageLayout = (pdf, contentItem, padding, pageWidth, yPosition) => {
    const contentWidth = 0.77 * pageWidth;
    const xOffset = (pageWidth - contentWidth) / 2;
  
    if (contentItem.imagePosition === "top") {
      yPosition = addImages(pdf, contentItem, xOffset, yPosition, contentWidth);
    }
  
    yPosition += addContentToPdf(pdf, contentItem.content, padding, yPosition, pageWidth);
    yPosition += addQuestionsToPdf(pdf, contentItem.questions, padding, yPosition, pageWidth);
  
    if (contentItem.imagePosition === "bottom") {
      yPosition = addImages(pdf, contentItem, xOffset, yPosition, contentWidth);
    }
  
    return yPosition;
  };
  
  const addImages = (pdf, contentItem, xOffset, yPosition, contentWidth) => {
    if (contentItem.image && contentItem.secondImage) {
      // Two side-by-side images
      const imageWidth = contentWidth * 0.5;
      const { height: imageHeight } = calculateImageDimensions(contentItem.image, imageWidth);
      const { height: secondImageHeight } = calculateImageDimensions(contentItem.secondImage, imageWidth);
  
      pdf.addImage(contentItem.image, "JPEG", xOffset, yPosition, imageWidth, imageHeight);
      pdf.addImage(
        contentItem.secondImage,
        "JPEG",
        xOffset + imageWidth,
        yPosition,
        imageWidth,
        secondImageHeight
      );
  
      return yPosition + Math.max(imageHeight, secondImageHeight) + 10; // Add spacing
    } else if (contentItem.image || contentItem.secondImage) {
      // Single image
      const image = contentItem.image || contentItem.secondImage;
      const { height: imageHeight } = calculateImageDimensions(image, contentWidth);
      pdf.addImage(image, "JPEG", xOffset, yPosition, contentWidth, imageHeight);
      return yPosition + imageHeight + 10; // Add spacing
    }
  
    return yPosition; // No images, return unchanged
  };

  return (
    <div>
      <button
        onClick={generatePdf}
        className="fixed bottom-4 bg-indigo-700 inline-flex right-4 text-white py-2 px-4 rounded hover:bg-indigo-600"
      >
        <span className="pr-2">
        <Download />
        </span>
         Download Exercises as PDF
      </button>
    </div>
  );
};

export default ExamPdf;