import React, {useState,useRef,useEffect} from 'react';
import api from '../utils/api';
import {Heart,Plus,Check } from 'lucide-react';
import StatusModal from '../UI/StatusModal';
import  {Snackbar}  from '@mui/material';
import ExerciseView from '../UI/ExerciseView';

const ExerciseCard = ({exercise, RenderComponent,favexercises, addedExercises}) => {
  const divRef = useRef(null);
  const [fontSize, setFontSize] = useState(12);

  useEffect(() => {
    const updateFontSize = () => {
      if (divRef.current) {
        const divWidth = divRef.current.offsetWidth;
        const a4Width = 780;
        const newFontSize = Math.max(12 * (divWidth / a4Width), 8);
        setFontSize(newFontSize);
      }
    };

    updateFontSize();
    window.addEventListener('resize', updateFontSize);
    return () => window.removeEventListener('resize', updateFontSize);
  }, [divRef]);


  const [error, setError] = useState(null);
  const [isErrorOpen, setIsErrorOpen] = useState(false);

  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');

  const handleAddFavorit = async () => {
    try {
      await api.post('/api/user/favorites/add', {exerciseId: exercise._id});
      setSnackbarMessage('added to favorit successfully.');
      setSnackbarOpen(true);
      setTimeout(RenderComponent,1500);
    } catch (error) {
      console.error("Error added to favorit service:", error);
      setError('Exercises has not added to favorit');
      setIsErrorOpen(true);
    }
  };
  const handleDeleteFavorit = async () => {
    try {
      await api.post('/api/user/favorites/delete', {exerciseId: exercise._id});
      setSnackbarMessage('deleted from favorit successfully.');
      setSnackbarOpen(true);
      setTimeout(RenderComponent,1500);
    } catch (error) {
      console.error("Error added to favorit service:", error);
      setError('Exercises has not delete from favorit');
      setIsErrorOpen(true);
    }
  };


  const handleAddExam = async () => {
    try {
      await api.post('/api/user/current/add',{exerciseId: exercise._id});
      setSnackbarMessage('the exercise add to exam successfully!');
      setSnackbarOpen(true);
      setTimeout(RenderComponent,1500);
    } catch (error) {
      console.error("Error Aadded in your exam Exercises:", error);
      setError('Exercise has not Aadded in your exam');
      setIsErrorOpen(true);
    }
  };
  const handleDeleteExam = async () => {
    try {
      await api.post('/api/user/current/delete',{exerciseId: exercise._id});
      setSnackbarMessage('the exercise from from exam successfully!');
      setSnackbarOpen(true);
      setTimeout(RenderComponent,1500);
    } catch (error) {
      console.error("Error Aadded in your exam Exercises:", error);
      setError('Exercise has not deleted from your exam');
      setIsErrorOpen(true);
    }
  };
  return (
    <>
      <div
        key={exercise._id}
        ref={divRef}
        className="w-full max-w-[794px] mx-auto bg-white border border-gray-300 rounded-lg shadow-lg mb-8"
        style={{ fontSize: `${fontSize}px` }}
      >
        <div className="exercise-section mb-6 px-8 pt-8">
          <div className="flex justify-between">
            <h1 className="text-2xl font-semibold mb-4">{exercise.title}</h1>
            <div className="flex gap-2">
              {favexercises.includes(exercise._id) ?(
              <button
                onClick={handleDeleteFavorit}
                className="inline-flex items-center justify-center rounded-md text-sm font-medium border border-input bg-background hover:bg-secondColor hover:border-firstColor hover:text-accent-foreground h-10 w-10 focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:outline-none transition-colors"
              >
                <Heart color="#f50000" fill='red'/>
              </button>
              ):(
                <button
                onClick={handleAddFavorit}
                className="inline-flex items-center justify-center rounded-md text-sm font-medium border border-input bg-background hover:bg-secondColor hover:border-firstColor hover:text-accent-foreground h-10 w-10 focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:outline-none transition-colors"
              >
                <Heart />
              </button>
              )
              }
              {addedExercises.includes(exercise._id) ?(
              <button
                onClick={handleDeleteExam}
                className="inline-flex items-center justify-center rounded-md text-sm font-medium border border-input bg-background hover:bg-secondColor hover:border-firstColor hover:text-accent-foreground h-10 w-10 focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:outline-none transition-colors"
              >
                <Check />
              </button>
              ):(
                <button
                onClick={handleAddExam}
                className="inline-flex items-center justify-center rounded-md text-sm font-medium border border-input bg-background hover:bg-secondColor hover:border-firstColor hover:text-accent-foreground h-10 w-10 focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:outline-none transition-colors"
              >
                <Plus />
              </button>
              )

              }
            </div>
          </div>
          <div>
            {exercise.exercise.map((item, itemIndex) => (
              <React.Fragment key={itemIndex}>
                <ExerciseView item={item} itemIndex={itemIndex} />
                <div className="bg-gray-300 px-8 pb-2 pt-3">
                  <div className="flex flex-wrap">
                    <p className="text-lg text-black mx-3 mb-2">
                      Level: {exercise.level.name}
                    </p>
                    <p className="text-lg text-black mx-3 mb-2">
                      Material: {exercise.material.name}
                    </p>
                    <p className="text-lg text-black mx-3 mb-4">
                      Difficulty: {exercise.difficulty}
                    </p>
                    <p className="text-lg text-black mx-3 mb-4">
                      Trimester: {exercise.Trimester}
                    </p>
                    <p className="text-lg text-black mx-3 mb-4">
                      Unit: {exercise.unit.name}
                    </p>
                  </div>
                </div>
              </React.Fragment>
            ))}
          </div>
        </div>
      </div>
      {error && (
        <StatusModal
          type="error"
          title="Error Occurred"
          message={error}
          isOpen={isErrorOpen}
          onClose={() => setIsErrorOpen(false)}
        />
      )}
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={() => setSnackbarOpen(false)}
        message={snackbarMessage}
      />
    </>
  );
  
};

export default ExerciseCard;