import { useState} from 'react'
import Trimesters from '../../Data/Trimesters'
import difficulty from '../../Data/difficulty'
import ExerciseCard from './ExerciseCard'
import useFetchData from '../utils/useFetchDataUser'
import { getFilterExercises } from '../utils/GetFliterExercises'

export default function FavoritesExercise() {

  const [selectedLevel, setSelectedLevelFilter] = useState('All')
  const [selectedTrimesters, setSelectedTrimesters] = useState('All')
  const [selectedDifficulty, setSelectedDifficulty] = useState('All')
  const [selectedMaterial, setSelectedMaterial] = useState('All')
  const [selectedUnit, setSelectedUnit] = useState('All')

  //fetch data from api
  const {
    levels,
    exercises,
    favexercises,
    addedExercises,
    setSelectedLevel,
    setSelectedMatriel,
    filteredMatriels,
    filteredUnits,
    loading, 
    error,
   refetch } = useFetchData();

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;
  const FavoriteExercises = getFilterExercises(exercises,favexercises)
  const filteredExercises = FavoriteExercises.filter(exercise => 
    (selectedLevel === 'All' || exercise.level._id === selectedLevel) &&
    (selectedTrimesters === 'All' || exercise.Trimester === selectedTrimesters) &&
    (selectedMaterial === 'All' || exercise.material._id === selectedMaterial) &&
    (selectedUnit === 'All' || exercise.unit._id === selectedUnit) &&
    (selectedDifficulty === 'All' || exercise.difficulty === selectedDifficulty)
  )

  return (
    <div className="container mx-auto p-4 bg-gray-100 min-h-screen">
      <header className="mb-8">
        <div className="flex justify-center items-center space-x-4">
          
          <span className='mr-[1px]'>Level:</span>
          <select 
            value={selectedLevel} 
            onChange={(e) => {
              const value=e.target.value;
              setSelectedLevelFilter(value);
              setSelectedLevel(value);
            }}
            className="p-2 border rounded bg-white text-gray-800"
          >
            <option key='All' value='All'>All</option>
            {levels.map(level => (
              <option key={level._id} value={level._id}>{level.name}</option>
            ))}
          </select>
          <span className='mr-[1px]'>Trimesters:</span>
          <select 
            value={selectedTrimesters} 
            onChange={(e) => setSelectedTrimesters(e.target.value)}
            className="p-2 border rounded bg-white text-gray-800"
          >
            <option key='All' value='All'>All</option>
            {Trimesters.map(Trimester => (
              <option key={Trimester} value={Trimester}>{Trimester}</option>
            ))}
          </select>
          <span className='mr-[1px]'>Material:</span>
          <select 
            value={selectedMaterial} 
            onChange={(e) =>{ 
              const value =e.target.value;
              setSelectedMaterial(value);
              setSelectedMatriel(value);
            }}
            className="p-2 border rounded bg-white text-gray-800"
          >
            <option key='All' value='All'>All</option>
            {filteredMatriels.map(item => (
              <option key={item._id} value={item._id}>{item.name}</option>
            ))}
          </select>
          <span className='mr-[1px]'>Unit:</span>
          <select 
            value={selectedUnit} 
            onChange={(e) => setSelectedUnit(e.target.value)}
            className="p-2 border rounded bg-white text-gray-800"
          >
            <option key='All' value='All'>All</option>
            {filteredUnits.map(item => (
              <option key={item._id} value={item._id}>{item.name}</option>
            ))}
          </select>
          <span className='mr-[1px]'>Difficulty:</span> 
          <select 
            value={selectedDifficulty} 
            onChange={(e) => setSelectedDifficulty(e.target.value)}
            className="p-2 border rounded bg-white text-gray-800"
          >
            <option key='All' value='All'>All</option>
            {difficulty.map(item => (
              <option key={item} value={item}>{item}</option>
            ))}
          </select>
        </div>
      </header>
      <h1 className="text-3xl font-bold text-center mb-4 text-gray-800">Exercises</h1>
      <div className="w-full">
        {filteredExercises.map(exercise => (
          <>
          <ExerciseCard key={exercise._id} 
           exercise={exercise} 
           favexercises={favexercises}
           addedExercises={addedExercises} 
           RenderComponent={refetch} />
          </>
        ))}
      </div>

    </div>
  )
}