import React from 'react';

const ExerciseView = ({ item,itemIndex }) => {
  
  const positionClassesMap = {
    right: 'flex-row',
    left: 'flex-row-reverse',
    top: 'flex-col-reverse',
    bottom: 'flex-col',
  };

  return (
    <div>
      <div className={`flex ${positionClassesMap[item.imagePosition] || ''} mb-5`}>
        <div className="exercise-content">
          <p className="content text-[length:var(--font-size)] whitespace-pre-wrap text-black font-bold mt-3 mb-4">
            {item.content}
          </p>
          <ol className="list-decimal ml-5 space-y-3 text-black leading-relaxed">
            {item.questions.map((q, qIndex) => (
              <li key={qIndex} className="font-semibold whitespace-pre-wrap mb-2">
                {q.question}
              </li>
            ))}
          </ol>
        </div>
        <div className="flex">
          {item.image && (
            <div
              className={`${
                item.image && item.secondImage ? 'w-1/2 m-3 my-auto' : 'w-2/3 mx-auto'
              }`}
            >
              <img
                src={item.image}
                alt={`Exercise illustration ${itemIndex}`}
                className="rounded shadow-md"
              />
            </div>
          )}
          {item.secondImage && (
            <div
              className={`${
                item.image && item.secondImage ? 'w-1/2 m-3 my-auto' : 'w-2/3 mx-auto'
              }`}
            >
              <img
                src={item.secondImage}
                alt={`Exercise illustration n2 ${itemIndex}`}
                className="rounded shadow-md"
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ExerciseView;